/* Blog Page */
.blogMain {
    position: relative;
    z-index: 15;
    background: $white; }
.blogBlock {
    font-family: $roboto;
    padding-top: 100px;
    padding-bottom: 50px;
    &__title {
        font-size: 30px;
        font-weight: 900;
        line-height: 33px;
        text-transform: uppercase;
        text-align: center;
        margin: 60px auto 60px auto;
        color: $blue;
        @include md(margin, 60px auto 30px auto); } }
.blogItem {
    &__btn {
        display: flex;
        justify-content: center; } }
.blogCart {
    @include row-flex();
    justify-content: center;
    align-items: flex-start;
    margin: 60px 0 60px 0;
    &__img {
        @include col();
        @include size(6);
        @include size-md(12);
        height: 300px;
        border-radius: 10px;
        overflow: hidden;
        @include md(order, 1);
        @include md(height, 200px);
        img {
            width: 100%;
            height: 100%;
            object-fit: cover; } }
    &__text {
        @include col();
        @include size(6);
        @include size-md(12);
        @include md(margin-bottom, 30px); }
    &__link {
        &-heading {
            font-weight: 900;
            font-size: 30px;
            line-height: 33px;
            text-transform: uppercase;
            color: $green;
            margin: 0;
            @include md(font-size, 24px); } }
    &__more {
        display: inline-block;
        font-size: $base-font-size;
        font-weight: normal;
        line-height: 22px;
        color: $blue;
        margin: 0 0 20px 0;
        border-bottom: 1px solid $blue;
        @include md(font-size, 14px); }
    &__descr {
        font-size: $base-font-size;
        font-weight: normal;
        line-height: 22px;
        color: $grey;
        margin: 24px 0;
        @include md(font-size, 14px);
        @include md(margin, 15px 0 15px 0); }
    &__date {
        font-style: $base-font-size;
        font-weight: normal;
        line-height: 22px;
        color: $grey;
        margin: 0 0 20px 0;
        @include md(font-size, 14px); }
    &__category {
        a {
            font-size: $base-font-size;
            font-weight: normal;
            line-height: 22px;
            color: $green;
            @include md(font-size, 14px); } } }
