/* Ecoprof Format Block */
.ecoprof-format-block {
    position: relative;
    padding: 50px 0 50px 0; }
.ecoprof-format {
    font-family: $roboto;
    &__heading {
        margin-bottom: 30px;
        @include md(text-align, center); }
    &__letter {
        font-weight: 900;
        font-size: 50px;
        line-height: 59px;
        text-transform: uppercase;
        @include md-block {
            font-size: 30px;
            line-height: 33px; }
        &_blue {
            color: $blue; }
        &_green {
            color: $green; } }
    &__content {
        @include row-flex();
        justify-content: space-between;
        align-items: flex-start;
        @include md(justify-content, center); }
    &__item {
        @include col();
        @include size(3.5);
        @include size-md(12);
        position: relative;
        @include md-block {
            &:nth-child(2) {
                margin-top: 50px; }
            &:nth-child(3) {
                margin-top: 50px; } } }
    &__text {
        position: relative;
        border-radius: 0px 25px;
        padding: 10px 30px;
        min-height: 140px;
        margin-bottom: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        font-weight: 500;
        line-height: 19px;
        color: $white;
        @include md(text-align, center);
        b {
            width: 100%;
            text-align: left;
            margin-bottom: 20px;
            @include lg(text-align, center); }
        hr {
            width: 100%;
            border: 1px solid $white; }
        &_blue {
            background: $blue;
            &::before {
                content: '';
                position: absolute;
                background: url('../img/ecoprof/treangle.png') no-repeat center;
                top: 50%;
                right: -85px;
                width: 60px;
                height: 70px;
                transform: translateY(-50%);
                @include md-block {
                    top: auto;
                    right: auto;
                    bottom: -100px;
                    transform: rotate(90deg) translateX(-50%); } } }
        &_grey {
            background: $grey;
            &::before {
                content: '';
                position: absolute;
                background: url('../img/ecoprof/treangle.png') no-repeat center;
                top: 50%;
                right: -85px;
                width: 60px;
                height: 70px;
                transform: translateY(-50%);
                @include md-block {
                    top: auto;
                    right: auto;
                    bottom: -100px;
                    transform: rotate(90deg) translateX(-50%); } } }
        &_green {
            background: $green; }
        &_big {
            min-height: 300px; } } }

