/* Ecofriends Achiv Block */
.ecofriends-achiv-block {
    padding: 50px 0 100px 0; }
.ecofriends-achiv {
    font-family: $roboto;
    &__heading {
        display: flex;
        @include md(justify-content, center); }
    &__letter {
        font-weight: 900;
        font-size: 50px;
        line-height: 59px;
        text-transform: uppercase;
        margin-bottom: 30px;
        @include md-block {
            font-size: 30px;
            line-height: 33px; }
        @include md-block {
            font-size: 24px;
            line-height: 27px; }
        &_blue {
            color: $blue; }
        &_green {
            color: $green;
            margin-left: 10px; } }
    &__content {
        @include row-flex();
        justify-content: space-between;
        align-items: flex-start;
        @include xl(justify-content, center); }
    &__item {
        @include col();
        @include size(4);
        @include size-xl(6);
        @include size-md(8);
        @include size-sm(12);
        @include size-xs(12);
        text-align: center;
        position: relative;
        @include md-block {
            &:nth-child(2) {
                margin-top: 100px; }
            &:nth-child(3) {
                margin-top: 100px; } }
        &::before {
            position: absolute;
            content: '';
            background: url('../img/ecofriends/lavr.png') no-repeat center;
            top: 0;
            right: 0;
            left: 0;
            bottom: -170px;
            @include md(bottom, -120px);
            @include sm-block {
                background-size: contain;
                bottom: -130px; } } }
    &__num {
        font-weight: 900;
        font-size: 100px;
        line-height: 117px;
        color: $blue;
        @include md-block {
            font-size: 80px;
            line-height: 89px; } }
    &__text {
        position: relative;
        min-height: 200px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        align-items: center;
        margin: 20px auto 0 auto;
        width: 55%;
        font-weight: normal;
        font-size: 14px;
        line-height: 19px;
        color: $grey;
        @include md(margin, 0 auto 0 auto);
        @include xs(width, 70%); } }

