/* Blog Single Page */
.blogContentBlock {
    padding-top: 150px;
    padding-bottom: 50px;
    @include md(padding-top, 100px); }
.blogContent {
    font-family: $roboto;
    &__heading {
        font-size: 30px;
        font-weight: 900;
        line-height: 33px;
        text-transform: uppercase;
        color: $blue;
        text-align: left;
        margin: 30px 0 20px 0;
        @include md(font-size, 24px); }
    p {
        font-size: 20px;
        line-height: 30px;
        color: $grey;
        @include md(font-size, 18px); }
    ul {
        font-size: 20px;
        color: $grey;
        line-height: 30px; } }
