/* Ecoprof Partners Block */
.ecoprof-partners-block {
    padding: 50px 0 50px 0;
    background: #F3F3F3; }
.ecoprof-partners {
    &__title {
        font-family: $roboto;
        font-size: 30px;
        font-weight: 900;
        line-height: 33px;
        text-align: center;
        text-transform: uppercase;
        color: $blue;
        margin: 0; }
    &__container {
        @include row-flex();
        justify-content: space-between;
        margin-top: 50px;
        @include md(justify-content, center); }
    &__logo {
        @include col();
        @include size(3);
        @include size-lg(3);
        @include size-md(6);
        @include size-sm(6);
        margin-bottom: 50px;
        @include md(text-align, center); } }
