/* Ecoprof Step Block */
.ecoprof-step-block {
    padding: 50px 0 50px 0;
    background: #F3F3F3; }
.ecoprof-step {
    font-family: $roboto;
    &__heading {
        margin-bottom: 50px;
        @include md(text-align, center); }
    &__letter {
        font-style: 500;
        font-weight: 900;
        font-size: 50px;
        line-height: 59px;
        text-transform: uppercase;
        @include md-block {
            font-size: 30px;
            line-height: 33px; }
        &_blue {
            color: $blue; }
        &_green {
            color: $green; } }
    &__content {
        @include row-flex();
        justify-content: space-between;
        @include md(justify-content, center); }
    &__item {
        @include col();
        @include size(3);
        @include size-xl(4);
        @include size-lg(4);
        @include size-md(8);
        @include size-sm(12); }
    &__text {
        margin-bottom: 30px; }
    &__title {
        background: $blue;
        border-radius: 25px 0px;
        padding: 10px 25px;
        font-size: 16px;
        line-height: 19px;
        font-weight: 500;
        text-transform: uppercase;
        display: flex;
        justify-content: space-around;
        color: $white;
        margin: 0 0 30px 0;
        @include md(justify-content, center); }
    &__list {
        padding-left: 18px;
        li {
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            list-style-type: decimal;
            color: $grey;
            margin-bottom: 30px; } } }
