/* Ecoprof Members Block */
.ecoprof-members-block {
    background: #F3F3F3;
    padding: 50px 0 50px 0; }
.ecoprof-members {
    &__heading {
        margin-bottom: 30px;
        @include md(text-align, center); }
    &__letter {
        font-weight: 900;
        font-size: 50px;
        line-height: 59px;
        text-transform: uppercase;
        @include md-block {
            font-size: 30px;
            line-height: 33px; }
        &_blue {
            color: $blue; }
        &_green {
            color: $green;
            margin-left: 10px; } }
    &__content {
        @include row-flex();
        justify-content: space-between; }
    &__text {
        @include col();
        @include size(5);
        @include size-lg(6);
        @include size-md(12);
        @include size-sm(12); }
    &__list {
        margin-bottom: 15px; }
    &__title {
        background: $blue;
        border-radius: 25px 0px;
        padding: 10px 25px;
        font-size: 16px;
        line-height: 19px;
        font-weight: 500;
        text-transform: uppercase;
        color: $white;
        text-align: center;
        margin-bottom: 15px; }
    &__descr {
        li {
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: $grey;
            padding: 5px 0; } }
    &__img {
        @include col();
        @include size(7);
        @include size-lg(6);
        @include size-md(12);
        @include size-sm(12);
        text-align: right;
        @include md(text-align, center);
        @include md(margin-top, 30px); } }
