/* Ecofriends System Block */
.ecofriends-system-block {
    background: #F3F3F3;
    min-height: 800px; }
.ecofriends-system {
    padding: 50px 0 0 0;
    &__heading {
        margin-bottom: 30px; }
    &__letter {
        display: block;
        font-weight: 900;
        font-size: 50px;
        line-height: 59px;
        text-transform: uppercase;
        @include md-block {
            font-size: 30px;
            line-height: 33px;
            text-align: center; }
        @include md-block {
            font-size: 24px;
            line-height: 27px; }
        &_blue {
            color: $blue; }
        &_green {
            color: $green; } }
    &__wrapp {
        @include row-flex();
        justify-content: space-between;
        align-items: flex-end; }
    &__content {
        @include col();
        @include size(6.20);
        @include size-sm(12);
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        @include sm(justify-content, center); }
    &__text {
        background: $blue;
        padding: 10px 30px;
        max-width: 320px;
        min-height: 140px;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        margin-bottom: 20px;
        font-family: $roboto;
        color: $white;
        font-size: 16px;
        line-height: 19px;
        b {
            font-size: 20px; }
        &:nth-child(1) {
            border-radius: 0px 25px;
            align-self: flex-start;
            flex-basis: 50%;
            @include sm-block {
                align-self: auto;
                border-radius: 25px 0px; } }
        &:nth-child(2) {
            border-radius: 0px 25px 25px 0px;
            align-self: flex-end;
            flex-basis: 50%;
            @include sm-block {
                align-self: auto;
                border-radius: 25px 0px; } }
        &:nth-child(3) {
            border-radius: 25px 0px 0px 25px;
            align-self: flex-start;
            flex-basis: 50%;
            @include sm-block {
                align-self: auto;
                border-radius: 25px 0px; } }
        &:nth-child(4) {
            border-radius: 0px 25px;
            align-self: flex-end;
            flex-basis: 50%;
            @include sm-block {
                align-self: auto;
                border-radius: 25px 0px; } } }
    &__img {
        @include col();
        @include size(5.5);
        @include size-sm(12);
        text-align: right;
        img {
            width: 100%;
            height: auto;
            object-fit: contain; } } }


