/* Ecoprof Result Block */
.ecoprof-result-block {
    padding: 50px 0 50px 0;
    &_grey {
        background: #F3F3F3; } }
.ecoprof-result {
    &__heading {
        margin-bottom: 30px;
        @include md(text-align, center); }
    &__letter {
        font-weight: 900;
        font-size: 50px;
        line-height: 59px;
        text-transform: uppercase;
        @include md-block {
            font-size: 30px;
            line-height: 33px; }
        &_blue {
            color: $blue; }
        &_green {
            color: $green; } }
    &__content {
        @include row-flex();
        @include md(justify-content, center); }
    &__text {
        @include col();
        @include size(5);
        @include size-lg(6);
        @include size-md(10);
        @include size-sm(12);
        @include md(margin-bottom, 30px); }
    &__list {
        li {
            position: relative;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: $grey;
            padding: 0 0 0 40px;
            margin-bottom: 20px;
            &::before {
                content: url('../img/ecoprof/check.svg');
                text-align: center;
                position: absolute;
                padding-top: 2px;
                width: 28px;
                height: 24px;
                top: 50%;
                left: 0px;
                transform: translateY(-50%);
                background: $blue;
                border-radius: 10px 0px; } } }
    &__img {
        @include col();
        @include size(7);
        @include size-lg(6);
        @include size-md(12);
        text-align: right;
        @include md(text-align, center); } }
