.header {
    position: absolute;
    width: 100%;
    z-index: 1001;
    background: linear-gradient(180deg, #324C64 0%, rgba(255, 255, 255, 0) 100%);
    @include lg-block {
        position: fixed;
        background: $grey; } }

.header-container {
    display: flex;
    justify-content: space-between;
    padding-top: 30px;
    @include lg-block {
        padding-top: 17px;
        padding-bottom: 17px; } }
/* Logo */
.logo {
    @include lg-block {
        display: flex;
        align-items: center; }
    &__img {
        display: block;
        width: 150px;
        height: 52px;
        img {
            object-fit: contain; } }
    &__text {
        font-family: $roboto;
        font-size: 12px;
        font-weight: 500;
        line-height: 14px;
        text-align: center;
        color: #FFFFFF;
        width: 150px;
        margin-top: 15px;
        @include lg-block {
            margin: 0 15px 0 15px; }
        @include sm(display, none); } }
/* Burger */
.burger {
    display: none;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    height: 40px;
    width: 50px;
    @include lg(display, flex);
    @include md(align-self, center);
    &__line {
        display: block;
        position: relative;
        width: 40px;
        height: 4px;
        background: $white;
        &::before,&::after {
            position: absolute;
            content: '';
            width: 40px;
            height: 4px;
            background: $white;
            border-radius: 100px; }
        &::before {
            bottom: 0;
            left: 0;
            margin-bottom: 14px; }
        &::after {
            top: 0;
            left: 0;
            margin-top: 14px; } } }
/* Header Menu */
.navigation-block {
    display: flex;
    align-items: flex-start;
    @include lg-block {
        position: fixed;
        width: 100vw;
        right: -100vw;
        background: rgba(0,0,0,0.85);
        height: 100vh;
        min-height: 100%;
        z-index: 1001;
        transition: right .5s;
        top: 86px; }
    @include lg-block {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center; }
    @include sm(top, 86px);
    &_active {
        @include lg-block {
            top: 86px;
            transition: right .5s;
            right: 0; }
        @include sm(top, 86px); } }
.menu-top {
    position: relative;
    z-index: 1001;
    ul {
        display: flex;
        align-items: center;
        text-align: center;
        list-style: none;
        padding: 0;
        margin: 0;
        @include lg-block {
            flex-direction: column;
            align-items: center;
            width: 100%;
            overflow: hidden; }
        li {
            position: relative;
            z-index: 10;
            font-family: $roboto;
            font-weight: 900;
            font-size: $base-font-size;
            line-height: 19px;
            padding: 12px 20px;
            @include lg-block {
                font-size: 24px;
                line-height: 27px;
                padding: 0;
                width: 100vw; }
            @include xs-block {
                font-size: 20px;
                line-height: 23px; }
            &::before {
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                height: 4px;
                width: 100%;
                border-radius: 10px;
                background: transparent;
                @include lg(display, none); }
            &:hover {
                color: $blue;
                &::before {
                    background: $blue; } }
            &:hover .menu-top__submenu {
                display: block;
                top: 40px;
                left: -10px;
                width: 120%;
                @include lg-block {
                    display: block;
                    position: relative;
                    transition: all 0.8s ease;
                    min-height: 100%;
                    height: 100%;
                    width: 100%;
                    left: auto;
                    top: auto;
                    right: 0;
                    transform: translateX(0); } }
            &:hover a {
                color: $blue; }
            .menu-top__submenu {
                position: absolute;
                display: flex;
                flex-direction: column;
                z-index: 5;
                display: none;
                @include lg-block {
                    position: relative;
                    display: block;
                    overflow: hidden;
                    min-height: 0;
                    height: 0;
                    top: 0;
                    right: -100%;
                    transition: all 0.5s ease;
                    transform: translateX(100%); }
                li {
                    font-family: $roboto;
                    font-weight: 500;
                    font-size: $base-font-size;
                    line-height: 19px;
                    padding: 15px 0;
                    border-radius: 10px;
                    margin-top: 9px;
                    background: $blue;
                    @include lg-block {
                        font-size: 20px; }
                    @include xs-block {
                        font-size: 18px;
                        line-height: 21px;
                        padding: 0; }
                    &:hover {
                        background: $green;
                        &::before {
                            background: none; } }
                    a {
                        color: $white;
                        @include md-block {
                            padding: 15px 0; } } } } } }
    a {
        display: block;
        line-height: 19px;
        color: $white;
        @include lg-block {
            padding: 30px 0; }
        @include xs-block {
            padding: 15px 0; } } }
