/* Ecoprof About Block */
.ecoprof-about {
    @include row-flex();
    font-family: $roboto;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 50px;
    margin-bottom: 70px;
    @include xl(align-items, center);
    @include lg(justify-content, center);
    &__text {
        @include col();
        @include size(4.7);
        @include size-lg(10);
        @include size-md(12); }
    &__wrapper {
        display: flex;
        @include sm(justify-content, center); }
    &__letter {
        font-size: 80px;
        font-weight: 900;
        line-height: 94px;
        text-transform: uppercase;
        @include xl-block {
            font-size: 50px;
            line-height: 59px; }
        @include md-block {
            font-size: 30px;
            line-height: 33px;
            text-align: center; }
        &_green {
            color: $green; }
        &_blue {
            color: $blue; } }
    &__descr {
        text-align: justify;
        font-weight: 500;
        font-size: $base-font-size;
        line-height: 19px;
        color: $grey;
        margin-top: 25px; }
    &__subdescr {
        position: relative;
        font-weight: 500;
        font-size: $base-font-size;
        line-height: 19px;
        text-align: justify;
        background: $blue;
        color: $white;
        z-index: 3;
        margin-top: 80px;
        @include md-block {
            margin-left: 30px;
            margin-right: 30px; }
        b {
            display: block;
            margin-bottom: 5px; }
        &::before {
            content: '';
            background: $blue;
            position: absolute;
            left: -30px;
            right: -30px;
            top: -30px;
            bottom: -30px;
            z-index: -1;
            border-radius: 25px 0px; } }
    &__img {
        @include col();
        @include size(7);
        @include size-lg(10);
        @include size-md(12);
        text-align: right;
        @include lg-block {
            text-align: center;
            margin-top: 50px; } } }
